@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+KR:wght@300;400&display=swap");

body {
  background-color: #f6f6f6;
  display: flex;
  justify-content: center;
  align-items: center; /*세로축 가운데*/
  font-family: "IBM Plex Sans KR", sans-serif;
  margin: 0px;
}

#root {
  background-color: white;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  height: 100vh;
  width: 80vh;
  margin: 0px;
}

@media (min-width: 650px) {
  #root {
    width: 650px;
  }
}

@media (max-width: 650px) {
  #root {
    width: 90%;
  }
}

.App {
  height: 100%;
  overflow: auto;
}

/* BUTTONS */
.Buttons {
  cursor: pointer;
  border: none;
  border-radius: 5px;

  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;
  min-width: 100px;
  font-size: 15px;
  white-space: nowrap;
  font-family: "IBM Plex Sans KR", sans-serif;
  justify-content: center;
}

.Buttons_default {
  background-color: #ececec;
  color: black;
}

.Buttons_positive {
  background-color: #64c964;
  color: white;
}

.Buttons_negative {
  background-color: #fd565f;
  color: white;
}

.Buttons_public {
  background-color: #6799ff;
  color: white;
}
/* HEADER */
header {
  /* overflow: auto; */
  height: 5%;
  color: #4c4c4c;
  padding-left: 20px;
  display: flex;
  align-items: center;
}

header > h2 {
  margin: 0px;
}
/* content-area*/
.content-area {
  background-color: beige;
  height: 90%;
  margin: 0;
  display: flex;
  align-items: flex-start;
  overflow: auto;
  justify-content: center;
}
.content-button > div {
  margin: 15px;
}
.content-button button {
  width: 150px;
  height: 50px;
  font-size: 18px;
}
.content-home {
  display: flex;
  align-items: center;
  justify-content: center;
}

.content-home > div {
  margin: 10px;
}

/* FOOTER */
footer {
  /* padding: 10px; */
  height: 5%;
  display: flex;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
}

footer > div {
  display: flex;
}
footer .footer_btn_center {
  /* width: 50%;
  font-size: 25px;
  justify-content: center; */
}

footer .footer_btn_left {
  width: 50%;
  font-size: 25px;
  justify-content: start;
}

footer .footer_btn_right {
  width: 50%;
  font-size: 25px;
  justify-content: end;
}

/* LOGIN */

.Login > #loginIdInput {
  margin-top: 10px;
  width: 400px;
  height: 40px;
  font-size: 20px;
}
.Login > div {
  padding-top: 50px;
  display: flex;
  justify-content: center;
}

/* PLACE */
.Place {
}

/* PlaceItem */

.PlaceItem {
  margin: 10px;
  padding: 5px;
  border-radius: 5px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border: 1px solid grey;
  background-color: #fcfcfc;
  cursor: pointer;
}

/* REVIEW ITEM */

.ReviewItem {
  margin: 10px;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid grey;
  background-color: #fcfcfc;
  /* cursor: pointer; */
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.Review {
  width: 550px;
}

.writeReview {
  /* position: absolute; */
  display: flex;
  justify-content: center;
  cursor: pointer;
}

.ReviewEditor > h2 {
  border-bottom: 1px solid black;
}
.ReviewEditor > div {
  margin-top: 20px;
  margin-bottom: 20px;
}
.photoContent {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 5px;
}
.photoContent > .Buttons {
  width: 120px;
  font-size: 15px;
}
.photoContent > input {
  margin-left: 10px;
  height: 40px;
  width: 450px;
}
.textContent {
  display: flex;
  justify-content: center;
  align-items: center;
}
.textContent > textarea {
  height: 80px;
  width: 580px;
}

.buttonContent {
  display: flex;
  align-items: center;
  justify-content: center;
}

.btnItem {
  display: flex;
  align-items: center;
  justify-content: center;
}

.btnItem > .Buttons {
  font-size: 15px;
  width: 80px;
  height: 40px;
  margin-left: 5px;
  margin-right: 5px;
}

/* MILEAGE */
.MileageItem {
  width: 500px;
  margin: 10px;
  padding: 5px;
  border-radius: 5px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border: 1px solid grey;
  background-color: #fcfcfc;
}
